.textContainer {
  width: 400px;
  height: max-content;
  padding: 5px 10px;
  border: 1px solid black;
  border-radius: 5px;
}
.hover:hover {
  background-color: black;
  color: white;
  transition: 0.4s;
}
.current {
  background-color: bisque;
}
.typing-demo {
  /* animation: typing 2s steps(22); */
  /* white-space: nowrap; */
  overflow: hidden;
  font-family: monospace;
  font-size: 2em;
}

@keyframes typing {
  0% {
    width: 0;
  }
  100% {
    width: 50px;
  }
}

/* @keyframes typing {
  from {
    width: 0;
  }
} */
@media (max-width: 1000px) {
  .textContainer {
    width: 330px;
  }
}
