.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  gap: 150px;
}
.pullQuestions {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.question {
  cursor: pointer;
}

@media (max-width: 1000px) {
  .App {
    flex-direction: column;
    gap: 50px;
    height: 100vh;
  }
  .pullQuestions{
    overflow-y: scroll;
  }
}
